



















































































































import uniInput from '../../../components/uni-input.vue';
//import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
import {
	API_DOMAIN
} from '../../../utils/config.js';
import {
	mapState,
	mapActions
} from "vuex";

export default {
	components: {
		uniInput,
		//mpvuePicker
	},
	computed: {
		...mapState(["token"])
	},
	data() {
		return {
			disabled: true,
			user: {
				NAME: '',
				PHONE: '',
				PASSWORD: '',
				//AVATAR: '',
				SEX: 0,
				AGE: '',
				ID_CARD_NO: '',
				Career: '',
				address: '',
				census: '',
				Social_security: '',
				Property: '',
				hunyin: '',
				car: '',
				Credit: '',
				bank_flow: '',
				owing: '',
				income: ''
			},
			sexList: [{
				sex: '男'
			}, {
				sex: '女'
			}],


		}
	},
	methods: {
		save() {
			if (!this.user.AGE) {
				this.Util.Toast.success("请填写年龄");
				return
			}
			this.API.updateUserinfo(this.user).then(res => {
				this.Util.Toast.success("保存成功");
				uni.setStorageSync('userInfo', JSON.stringify(res.data));
				setTimeout(() => {
					//uni.navigateBack()
					uni.navigateTo({
						url: '/pages/apply/apply'
					});
				}, 500)
			})
		},
		sexChange(evt) {
			var id = parseInt(evt.detail.value);
			this.user.SEX = id;
		},
		changeDisabled(){
			var flag = true;
			var user = this.user;
			for (var key in user) {
				if (user[key] === '') {
					flag = false;
				}
			}
			this.disabled = flag ? false : true;
		},
		bindInput(value) {
			//this.changeDisabled()
		},
	},
	onLoad(option) {
		var title = option.title;
		if(title){
			uni.setNavigationBarTitle({
				title
			});
		}
	},
	onShow() {
		// 登陆后个人信息存在本地的userInfo
		if (this.token) {
			var userInfo = JSON.parse(uni.getStorageSync('userInfo'));
			var newUserInfo = {
				hunyin: '',
				owing: '',
				address: '',
				PASSWORD: '',
				...userInfo.customer,
				...userInfo.user,
			};
			var user = {};
			for (var key in this.user) {
				user[key] = newUserInfo[key] == 'null' ? '' : newUserInfo[key] ;
			}
			this.user = user;
			//this.changeDisabled()
		}

	}

}
